  --bs-dropdown-min-width: 17.5rem;
}
.dropdown-modifier__body {
  max-height: 15.625rem;
  overflow-y: auto;
  scrollbar-color: transparent transparent;
  scrollbar-width: none;
}
.dropdown-modifier__body::-webkit-scrollbar {
